<template>
    <v-select
            v-bind:items="items"
            v-model="roleValue"
            label=""
            @change="$emit('change', $event)"
    ></v-select>
</template>

<script>
    export default {

        name: "RoleSelector",
        props:{
            role:String
        },
        data:()=>({
            roleValue:undefined,
            items:[
                {text:'',value:'INDIVIDUAL'},
                {text:'',value:'AUCTIONEER'},
                {text:'',value:'ANTIQUE_STORE'},
                {text:'',value:'ARCH_DEPT'},
                {text:'',value:'JUDICIAL_DEPT'},
                {text:'',value:'ADMIN'}
            ]
        }),
        mounted() {
            this.roleValue = this.role
        }
    }
</script>

